body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.App{text-align:center}.App .header{background-color:#e0ddd7;color:#000000;padding:50px 150px;text-align:left;font-size:24px}.App .header span{background-color:#f1756b;border-radius:50px;padding:20px;transform:rotate(-10deg);display:inline-block}.App .header .other{display:flex;margin-top:20px;text-align:center}.App .header .other div{flex:1 1}.App .header .other div img{width:100%}.App .header .other div a{color:black;text-decoration:none}.App .profile{background:url(/github-profile/public/static/media/profile_bg.261f1284.jpg) center center no-repeat;min-height:600px;background-attachment:fixed;background-size:cover;padding:50px 0 40px;-webkit-filter:contrast(0.8);filter:contrast(0.8)}.App .profile .block{height:200px;width:60%;display:inline-block;margin:60px 0;position:relative;background-color:#465861;border-radius:20px}.App .profile .block .profile_name{display:inline-block;position:absolute;background-color:#441b18;color:white;width:200px;font-size:30px;border-radius:30px;left:0;top:0;transform:translate(-50%, -50%);box-shadow:2px 1px 1px 1px #d8ada0}.App .profile .block .profile_content{padding:30px 120px;color:white;font-size:18px}.App .profile .block .profile_content a{color:white;text-decoration:none}

